.hero-section {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: hidden;
  background-color: #000000;
  color: #FFFFFF;

}

.circle {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.2), transparent);
  pointer-events: none;
}

.circle-1 { width: 300px; height: 300px; top: 20%; left: 30%; }
.circle-2 { width: 450px; height: 450px; top: 15%; left: 25%; }
.circle-3 { width: 600px; height: 600px; top: 10%; left: 20%; }

.hero-title {
  font-size: 4rem;
  margin: 10px 0;
  font-weight: bold;
}

.cta-button {
  padding: 15px 30px;
  font-size: 1.2rem;
  background-color: #6E00CF;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.cta-button:hover {
  transform: scale(1.1);
  background-color: #32005F;
}

.cta-button:focus {
  outline: 2px solid #fff;
  outline-offset: 2px;
}

@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }

  .circle-1 { width: 150px; height: 150px; }
  .circle-2 { width: 225px; height: 225px; }
  .circle-3 { width: 300px; height: 300px; }
}