.scroll-reveal-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 60px 20px;
  background-color: #fff;
}

.reveal-text {
  font-size: 1.8rem;
  opacity: 0;
  transform: translateY(50px);
  text-align: center;
  max-width: 600px;
}

@media (max-width: 768px) {
  .reveal-text {
    font-size: 1.4rem;
  }
}