.marquee-section {
  padding: 40px 20px;
  background-color: #fff;
  overflow: hidden;
}

.marquee {
  display: flex;
  white-space: nowrap;
}

.marquee-logo {
  width: 150px;
  height: 50px;
  margin: 0 20px;
  object-fit: contain;
}

@media (max-width: 768px) {
  .marquee-logo {
    width: 100px;
    height: 40px;
  }
}